import { TypographySystem } from '@mui/joy/styles/types/typography';
import Typography from '@mui/joy/Typography/Typography';
import { useEffect, useRef, useState } from 'react';

interface WordProps {
  children: string;
  extraLarge: boolean;
  fadeInDelay?: number;
  level: string;
  show?: boolean;
  style?: React.CSSProperties;
  textBackground?: string;
  weight: number;
}

const Word = ({ children, extraLarge, fadeInDelay, level, show, style, textBackground, weight }: WordProps) => {
  const [opacityLevel, setOpacityLevel] = useState<number>(0);
  const baseStyles = {
    opacity: opacityLevel,
    color: style?.color || 'white',
    paddingLeft: '.35rem',
    paddingRight: '.35rem',
    display: 'inline-block',
    textShadow: style?.textShadow || '0px 1px 0px #000, 0px 2px 0px #000, 0px 3px 0px #0000',
    transition: 'opacity ease-in 200ms',
  };
  const cssStyles = extraLarge
    ? {
        ...baseStyles,
        fontSize: '300%',
        backgroundColor: textBackground || 'transparent',
      }
    : baseStyles;

  useEffect(() => {
    if (show !== true) {
      return;
    }

    setTimeout(() => {
      setOpacityLevel(1);
    }, fadeInDelay);
  }, [fadeInDelay, show]);

  return (
    <Typography level={level as keyof TypographySystem} style={cssStyles} fontWeight={weight}>
      {children}
    </Typography>
  );
};

export interface AnimatedTextProps {
  children: string;
  extraLarge?: boolean;
  level: string;
  weight: number;
  speed?: number;
  style?: React.CSSProperties;
  textBackground?: string;
}

export function AnimatedText({
  children,
  extraLarge = false,
  level,
  speed = 100,
  style,
  textBackground,
  weight,
}: AnimatedTextProps) {
  const containerRef = useRef<HTMLDivElement>(null);
  const [show, setShow] = useState<boolean>();
  const [observer, setObserver] = useState<IntersectionObserver>();

  useEffect(() => {
    if (show === true || !containerRef?.current) {
      return;
    }

    if (!observer) {
      setObserver(
        new window.IntersectionObserver(
          ([entry]) => {
            if (entry.isIntersecting) {
              setShow(true);
              return;
            }
          },
          {
            root: null,
            threshold: 0.1, // set offset 0.1 means trigger if atleast 10% of element in viewport
          }
        )
      );
    }

    observer?.observe(containerRef.current);

    return () => {
      observer?.disconnect();
    };
  }, [observer, show]);

  const content = children.split(' ').map((word, index) => (
    <Word
      key={`${word}-${index}`}
      level={level}
      weight={weight}
      fadeInDelay={speed * index}
      show={show}
      extraLarge={extraLarge}
      style={style}
      textBackground={textBackground}
    >
      {word}
    </Word>
  ));

  return (
    <div ref={containerRef} style={style}>
      {content}
    </div>
  );
}
