import Typography from '@mui/joy/Typography/Typography';
import Card from '@mui/joy/Card';
import Grid from '@mui/joy/Grid';
import styled from '@emotion/styled';
import React, { useEffect, useState } from 'react';

const Container = styled.div`
  transition: 200ms;
  box-sizing: border-box;
  position: fixed;
  width: 100%;
  z-index: 1;
`;

const Nav = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  column-gap: 2rem;
`;

const Link = styled(Typography)`
  transition: 200ms;
  borer-bottom: 2px solid transparent;
  cursor: pointer;

  &:hover {
    border-bottom: 2px solid black;
  }
`;

const NavElement = ({ children }: { children: React.ReactNode }) => <Link level="title-md">{children}</Link>;

export default function Header() {
  const [isTopOfPage, setIsTopOfPage] = useState<boolean>(true);

  useEffect(() => {
    setIsTopOfPage(window.scrollY <= 0);
    const handleScroll = () => {
      setIsTopOfPage(window.scrollY <= 0);
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  return (
    <Container
      style={{
        padding: isTopOfPage ? '10px' : '0',
        borderBottom: isTopOfPage ? '2px solid transparent' : '2px solid black',
      }}
    >
      <Card
        size="md"
        variant="outlined"
        style={{
          transition: '200ms',
          background: isTopOfPage ? 'transparent' : '#ffffffe8',
          border: isTopOfPage ? '2px solid black' : '2px solid transparent',
          borderRadius: isTopOfPage ? '8px' : '0px',
          padding: isTopOfPage ? '16px' : '26px',
        }}
      >
        <Grid container spacing={2} sx={{ flexGrow: 1 }}>
          <Grid sm={3}>
            <Typography level="h4">Real Zero</Typography>
          </Grid>
          <Grid sm={9}>
            <Nav>
              <NavElement>About Us</NavElement>
              <NavElement>7 Acts</NavElement>
              <NavElement>Our Work</NavElement>
              <NavElement>Our Team</NavElement>
              <NavElement>News</NavElement>
              <NavElement>Connect</NavElement>
            </Nav>
          </Grid>
        </Grid>
      </Card>
    </Container>
  );
}
